/* --------------------------------------------------------------------------
   Imports
-------------------------------------------------------------------------- */
@import url('https://fonts.googleapis.com/css2?family=Playwrite+AU+QLD:wght@100..400&display=swap');

/* --------------------------------------------------------------------------
   Global Styles
   Sets background gradient, font, and base color for the whole page.
-------------------------------------------------------------------------- */
body {
  background: linear-gradient(
    120deg,
    #0f0026 0%,
    #172e94 35%,
    #5f1d7a 60%,
    #266da7 80%,
    #1e2d5d 100%
  );
  min-height: 100vh;
  margin: 0;
  font-family: 'Playwrite AU QLD', Arial, sans-serif;
  color: #f3eaff;
}

/* --------------------------------------------------------------------------
   Header Block Styles
   Main header block at the top of the page.
   Includes border, rounded corners, animated gradient background, and glow.
   Stays at the top when scrolling (sticky).
-------------------------------------------------------------------------- */
.header {
  margin: 20px;
  padding: 20px;
  border: 6px solid #5e04b3;
  border-radius: 24px;
  box-shadow:
    0 10px 43px 0px #4a37f3,                /* Blue glow around header */
    0 5px 21px 0px rgba(97, 130, 240, 0.445),    /* Softer blue glow */
    0 10px 32px -5px rgba(202, 185, 248, 0.452);  /* Diffused blue glow */
  background: linear-gradient(
    120deg,
    rgba(15, 0, 38, 0.75) 0%,
    rgba(23, 46, 148, 0.75) 35%,
    rgba(95, 29, 122, 0.75) 60%,
    rgba(38, 109, 167, 0.75) 80%,
    rgba(30, 45, 93, 0.75) 100%
  );
  background-size: 600% 600%;
  animation: gradientMove 15s ease-in-out infinite;
  box-sizing: border-box;
  position: sticky; /* Keeps header at top while scrolling */
  top: 0px;
  z-index: 1000;
  overflow: hidden;
  transition:
    border-color 0.4s cubic-bezier(.4,2,.6,1),
    box-shadow 0.4s,
    opacity 1.5s cubic-bezier(.4,2,.6,1),
    transform 1.5s cubic-bezier(.4,2,.6,1);
  opacity: 1;
  transform-origin: 56px 80px; /* Center for scaling/shrinking animation */
  transform: scale(1);
}

/* --------------------------------------------------------------------------
   Header Hover/Focus
   Changes border and glow for interactivity.
-------------------------------------------------------------------------- */
.header:hover, .header:focus-within {
  border-color: #5f1d7a;
  box-shadow: 0 12px 48px rgba(167,38,147,0.45);
}

/* --------------------------------------------------------------------------
   Animated Gradient Keyframes
   Used for animated gradient background in header.
-------------------------------------------------------------------------- */
@keyframes gradientMove {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* --------------------------------------------------------------------------
   Header Collapse/Expand Button Styles
   Button to collapse the header, positioned top-right inside header.
-------------------------------------------------------------------------- */
.header-collapse-btn {
  position: absolute;
  top: 8px;
  right: 24px;
  z-index: 3000;
  background: #5f1d7a;
  color: #fff;
  border: none;
  border-radius: 999px;
  padding: 8px 18px;
  font-size: 1em;
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 2px 8px rgba(38,109,167,0.18);
  transition: background 0.2s, color 0.2s;
}

/* Collapse button hover/focus: color change for feedback */
.header-collapse-btn:hover,
.header-collapse-btn:focus {
  background: #266da7;
  color: #fff;
}

/* --------------------------------------------------------------------------
   Collapsed Header Styles
   Hides content and shrinks header.
-------------------------------------------------------------------------- */
.header.collapsed {
  opacity: 0;
  height: 36px !important;
  overflow: hidden;
  padding: 0 !important;
  transition:
    opacity 5s cubic-bezier(.4,2,.6,1),
    height 5s cubic-bezier(.4,2,.6,1),
    padding 5s cubic-bezier(.4,2,.6,1);
}

/* Hide carousel or other content when header is collapsed */
.header.collapsed .carousel-placeholder {
  display: none !important;
}

/* --------------------------------------------------------------------------
   Floating Expand Button Styles
   Floating button to expand header, styled with animated glow and gradients.
-------------------------------------------------------------------------- */
.header-float-expand-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 32px;
  left: 100px;
  z-index: 3001;
  background: transparent;
  color: #f3eaff;
  border: 2px solid #5f1d7a;
  border-radius: 999px;
  padding: 0;
  width: 56px;
  height: 56px;
  font-size: 2em;
  font-family: 'Playwrite AU QLD', cursive;
  font-weight: bold;
  cursor: pointer;
  box-shadow:
    0 0 16px 4px #c0a4e66c,
    0 4px 16px rgba(120,120,180,0.15),
    0 8px 24px -4px rgba(180,180,255,0.12);
  transition: 
    background 0.2s, 
    color 0.2s, 
    box-shadow 0.2s, 
    border-color 0.4s cubic-bezier(.4,2,.6,1);
  outline: none;
  margin-bottom: 6px;
  background-clip: padding-box;
  overflow: visible;
}

/* Floating expand button: animated glowing border using ::before */
.header-float-expand-btn::before {
  content: "";
  position: absolute;
  inset: -4px;
  border-radius: 999px;
  z-index: -2;
  background: linear-gradient(
    120deg,
    #172e94 0%,
    #5f1d7a 40%,
    #266da7 70%,
    #1e215d 100%
  );
  background-size: 600% 600%;
  animation: tabBorderMoveReverse 15s ease-in-out infinite;
  filter: blur(0.5px) brightness(1.1);
}

/* Floating expand button: animated gradient background using ::after */
.header-float-expand-btn::after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 999px;
  z-index: -1;
  background: linear-gradient(
    120deg,
    #1e215d 0%,
    #266da7 40%,
    #5f1d7a 70%,
    #172e94 100%
  );
  background-size: 600% 600%;
  animation: tabBgMove 11s ease-in-out infinite reverse;
  opacity: 0.95;
  transition: opacity 0.3s;
}

/* Expand button hover/focus: increases glow intensity */
.header-float-expand-btn:hover::after,
.header-float-expand-btn:focus::after {
  opacity: 1;
  filter: brightness(3);
}

/* Keyframes for animated gradients on expand button */
@keyframes tabBorderMoveReverse {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes tabBgMove {
  0% { background-position: 100% 50%; }
  50% { background-position: 0% 50%; }
  100% { background-position: 100% 50%; }
}

/* --------------------------------------------------------------------------
   Header Content Visibility
   Controls fade-in/fade-out of header content.
-------------------------------------------------------------------------- */
.header-content {
  opacity: 0;
  transition: opacity 5s cubic-bezier(.4,2,.6,1);
}

.header-content.visible {
  opacity: 1;
}

/* --------------------------------------------------------------------------
   Header Shrink/Collapse Animation
   Used for animated shrinking/collapsing of header.
-------------------------------------------------------------------------- */
.header.shrink-out,
.header.collapsed {
  opacity: 0;
  transform: scale(0.1);
  pointer-events: none;
  transition:
    opacity 1.5s cubic-bezier(.4,2,.6,1),
    transform 1.5s cubic-bezier(.4,2,.6,1);
}
